/*
 * @Author: xjn 
 * @Date: 2022-09-05 13:51:52 
 * @Last Modified by: xjn
 * @Last Modified time: 2022-09-06 17:33:57
    被保人信息录入
 */
<template>
  <div class="top">
    <div class="insured-people">
      <h3 style="display: flex; justify-content: left; align-items: center">
        <!-- <van-icon style="color: red" name="award" /> -->
        <span class="top_blue"></span>
        投保人信息
      </h3>
      <div class="form">
        <van-form
          @submit="onSubmit"
          onkeydown="if(event.keyCode==13)return false;"
        >
          <van-field
            v-model="form.userName"
            name="userName"
            label=""
            maxlength="20"
            :disabled="userNameDisabled"
            placeholder="投保人姓名"
            :rules="[
              { required: true, message: '请输入投保人姓名' },
              { validator: userNameValidator, message: '姓名格式不正确' },
            ]"
            left-icon="friends-o" />
          <van-field
            v-model="form.idCard"
            name="idCard"
            label=""
            maxlength="18"
            :disabled="idCardDisabled"
            placeholder="身份证号码"
            :rules="[
              { required: true, message: '请输入身份证号码' },
              { validator: idCardValidator, message: '身份证号码格式不正确' },
            ]"
            left-icon="idcard"
          >
            <!-- 身份证的扫描 -->
            <!-- <template #button>
              <van-icon name="scan" @click="scanIdCard" /> </template
          >
           -->
          </van-field>

          <van-field
            readonly
            clickable
            name="dateTime"
            :value="form.dateTime"
            label=""
            placeholder="请选择身份证有效期"
            @click="DateShowPicker = true"
            :rules="[{ required: true, message: '请选择身份证有效期' }]"
            left-icon="tosend"
            right-icon="arrow"
          >
            <!-- 点击长期 -->
            <template #button>
              <div class="longTime" @click.stop="longTime">长期</div>
            </template>
          </van-field>
          <!-- 时间选择器弹框 -->
          <van-popup v-model="DateShowPicker" position="bottom">
            <van-datetime-picker
              v-model="currentDate"
              type="date"
              title="选择年月日"
              :min-date="cardDate"
              :max-date="maxDate"
              @confirm="DateOnConfirm"
              @cancel="DateShowPicker = false"
            />
          </van-popup>
          <!-- 手机号 -->
          <van-field
            v-model="form.phone"
            name="phone"
            label=""
            maxlength="11"
            type="digit"
            placeholder="手机号码"
            :disabled="phoneDisabled"
            @change="formPhoenChange"
            :rules="[
              { required: true, message: '请输入手机号码' },
              { validator: phoneValidator, message: '手机号码格式不正确' },
            ]"
            left-icon="phone-o" />
          <van-field
            v-model="form.SMSCode"
            name="SMSCode"
            label=""
            type="digit"
            maxlength="6"
            placeholder="6位短信验证码"
            :rules="[
              { required: true, message: '请输入6位短信验证码' },
              { validator: SMSCodeValidator, message: '请输入6位短信验证码' },
            ]"
            left-icon="chat-o"
          >
            <template #button>
              <!-- <button @click.prevent="getSMSCode" type="button">asfas</button> -->
              <van-button
                @click.prevent="getSMSCode"
                type="info"
                size="small"
                plain
                :disabled="SMSCodeDisable"
                >{{ SMSCodeButText }}</van-button
              >
              <!-- <span style="color: #4ca2fe" @click.prevent="getSMSCode">
                {{ SMSCodeButText }}
              </span> -->
            </template>
          </van-field>

          <!-- 短信滑块验证的弹窗 -->
          <van-overlay
            :show="showOverlay"
            @click="showJigsaw && (showOverlay = false)"
          >
            <transition name="fade" mode="out-in">
              <jigsaw @success="jigsawSuccess" v-if="showJigsaw"></jigsaw>
            </transition>
          </van-overlay>

          <!-- 邮箱 -->
          <van-field
            v-model="form.email"
            :disabled="emailDisabled"
            name="email"
            maxlength="30"
            label=""
            placeholder="常用邮箱"
            :rules="[{ validator: emailValidator, message: '邮箱格式不正确' }]"
            left-icon="envelop-o"
          >
          </van-field>

          <!--       被保人信息 -->
          <div class="be-people">
            <h3
              style="display: flex; justify-content: left; align-items: center"
            >
              <!-- <van-icon style="color: red" name="award" /> -->
              <span class="top_blue"></span>
              被保人信息
            </h3>
            <van-field
              v-model="form.userNameInsured"
              name="userNameInsured"
              label=""
              :disabled="userNameInsuredDisabled"
              placeholder="被保人姓名"
              maxlength="20"
              :rules="[
                { required: true, message: '请输入被保人姓名' },
                {
                  validator: userNameInsuredValidator,
                  message: '姓名格式不正确',
                },
              ]"
              left-icon="friends-o"
            />
            <van-field
              v-model="form.idCardInsured"
              name="idCardInsured"
              label=""
              placeholder="身份证号码"
              :disabled="idCardInsuredDisabled"
              maxlength="18"
              :rules="[
                { required: true, message: '请输入被保人身份证号码' },
                {
                  validator: idCardValidatorInsured,
                  message: '身份证号码格式不正确',
                },
              ]"
              left-icon="idcard"
            >
              <!-- 身份证的扫描 -->
              <!-- <template #button>
                <van-icon name="scan" @click="scanIdCard" /> </template
            > -->
            </van-field>

            <van-field
              readonly
              clickable
              name="dateTimeInsured"
              :value="form.dateTimeInsured"
              label=""
              placeholder="请选择身份证有效期"
              @click="DateShowPickerInsured = true"
              :rules="[{ required: true, message: '请选择身份证有效期' }]"
              left-icon="tosend"
              right-icon="arrow"
            >
              <!-- 点击长期 -->
              <template #button>
                <div class="longTime" @click.stop="longTimeInsured">长期</div>
              </template>
            </van-field>
            <!-- 时间选择器弹框 -->
            <van-popup v-model="DateShowPickerInsured" position="bottom">
              <van-datetime-picker
                v-model="currentDateInsured"
                type="date"
                title="选择年月日"
                :min-date="cardDate"
                :max-date="maxDate"
                @confirm="DateOnConfirmInsured"
                @cancel="DateShowPickerInsured = false"
              />
            </van-popup>
          </div>

          <template v-if="judgeParam == 'true' ? false : true">
            <!-- 学校 -->

            <van-field
              v-model="form.school"
              name="school"
              :disabled="true"
              label=""
              placeholder="请输入学校"
              :rules="[{ required: true, message: '请输入学校' }]"
              left-icon="wap-home-o"
            />

            <!-- 选择年级 -->
            <van-field
              readonly
              clickable
              name="gradeChoose"
              :value="form.gradeChoose"
              label=""
              placeholder="请选择年级"
              @click="gradeChooseShowPicker = true"
              :rules="[{ required: true, message: '请选择年级' }]"
              left-icon="user-o"
            >
              <template #button v-if="!this.form.gradeChoose">
                <span class="chioce">请选择 </span> <van-icon name="arrow" />
              </template>
            </van-field>

            <!-- 选择年级弹框 -->
            <van-popup v-model="gradeChooseShowPicker" position="bottom">
              <van-picker
                show-toolbar
                :columns="gradeColumns"
                @confirm="gradeChooseOnConfirm"
                @cancel="gradeChooseShowPicker = false"
              />
            </van-popup>

            <!-- 选择班级 -->
            <van-field
              readonly
              clickable
              name="classChoose"
              :value="form.classChoose"
              :rules="[{ required: true, message: '请选择班级' }]"
              label=""
              placeholder="请选择班级"
              @click="
                form.gradeChoose
                  ? (classChooseShowPicker = true)
                  : ((classChooseShowPicker = false), $toast('请选择年级！'))
              "
              left-icon="cluster-o"
            >
              <template #button v-if="!this.form.classChoose">
                <div>
                  <span class="chioce">请选择 </span> <van-icon name="arrow" />
                </div>
              </template>
            </van-field>

            <!-- 选择班级弹框 -->
            <van-popup v-model="classChooseShowPicker" position="bottom">
              <van-picker
                show-toolbar
                :columns="classColumns"
                @confirm="classChooseOnConfirm"
                @cancel="classChooseShowPicker = false"
              />
            </van-popup> </template
        ></van-form>
      </div>
    </div>
  </div>
</template>
          <!-- 表单项类型 - 省市区选择器 -->
          <!-- <van-field
            readonly
            clickable
            name="area"
            :value="form.area"
            label=""
            placeholder="点击选择省市区"
            left-icon="location-o"
            @click="showArea = true"
            :rules="[{ required: true, message: '请选择省市区' }]"
          /> -->
          <!-- <van-popup v-model="showArea" position="bottom">
            <van-area
              :area-list="areaList"
              @confirm="AreaonConfirm"
              @cancel="showArea = false"
            />
          </van-popup> -->
          <eArea
            :areaJude="areaJude"
            :value="productData.plchdDistrictCode"
            :cityCode="productData.plchdCityCode"
            :readonly="true"
          ></eArea>

          <!--下一步  -->
          <div style="margin: 16px">
            <van-button
              round
              block
              type="info"
              native-type="submit"
              @click="gotoInsuranceApplication"
              >下一步</van-button
            >
          </div>
        </van-form>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from "vant";
import Vue from "vue";
// import SlideVerify from "vue-monoplasty-slide-verify";
import jigsaw from "@/components/jigsaw";
import eArea from "@/components/area";

// Vue.use(SlideVerify);
import isIdcard from "../../../views/whiteList/JS/index";
import areaList from "./areaList";
import img1 from "../../../assets/img/验证/img.jpg";
import img2 from "../../../assets/img/验证/img2.jpg";
import util from "@/components/_utils";

export default {
  name: "",
  components: { jigsaw, eArea },

  data() {
    return {
      areaJude: true,
      cardDate: new Date(+new Date() + 86400000),
      slideImgs: [img1, img2], // 可自定义拼图图片数组，未传则使用插件内默认的图片数组
      // 表单的内容
      showOverlay: false,
      showJigsaw: false,
      gradeColumnsClass: [],
      classColumnsClass: [],
      classArray: [],
      dataList: {},
      productData: {},
      batchno: "",
      form: {
        classId: "",
        userName: "",
        idCard: "",
        dateTime: "",
        userNameInsured: "",
        idCardInsured: "",
        dateTimeInsured: "",

        phone: "",
        SMSCode: "",
        school: "",
        gradeChoose: "",
        classChoose: "",
        email: "",
        area: "",
      },

      //   日期弹框
      DateShowPicker: false,
      DateShowPickerInsured: false,
      minDate: new Date(1900, 1, 1),
      maxDate: new Date(2100, 1, 1),
      currentDate: "",
      currentDateInsured: "",

      //   短信验证弹框
      slideVerifyShow: false,
      slideVerifyText: "滑动完成拼图",
      SMSCodeDisable: false,
      SMSCodeButText: "发送验证码",
      // 的禁用
      idCardDisabled: false,
      userNameDisabled: false,
      userNameInsuredDisabled: false,
      phoneDisabled: false,
      emailDisabled: false,

      //   选择年级
      gradeColumns: [
        // "一年级",
        // "二年级",
        // "三年级",
        // "四年级",
        // "五年级",
        // "六年级",
      ],
      gradeChooseShowPicker: false,
      //   选择年级
      classColumns: [
        // "一班", "二班", "三班", "四班", "五班", "六班"
      ],
      classChooseShowPicker: false,
      // 地区
      showArea: false,
      areaList: areaList, // 数据格式见 Area 组件文档
      // imageCode
      imageCode: "",
      // 判断是否隐藏学校信息
      judgeParam: JSON.parse(sessionStorage.getItem("planPage")).judgeParam,
    };
  },
  created() {
    // 本地存储的from值 ,赋值
    this.productData = JSON.parse(sessionStorage.getItem("productData"));
    console.log(
      this.productData.plchdCityCode,
      "----",
      this.productData.plchdDistrictCode
    );
    if (JSON.parse(sessionStorage.getItem("MORE_PAGE_from"))) {
      this.form = JSON.parse(sessionStorage.getItem("MORE_PAGE_from"));
    }
    if (JSON.parse(sessionStorage.getItem("whiteList_json")).IDcard) {
      this.idCardInsuredDisabled = true;
      this.form.idCardInsured = JSON.parse(
        sessionStorage.getItem("whiteList_json")
      ).IDcard;
    }
    // 白名单存储
    if (JSON.parse(sessionStorage.getItem("productData")).plchdNm) {
      this.userNameInsuredDisabled = true;
      this.form.userNameInsured = JSON.parse(
        sessionStorage.getItem("productData")
      ).plchdNm;
    }
    // if (JSON.parse(sessionStorage.getItem("productData")).plchdMoveTelNo) {
    //   this.phoneDisabled = true;

    //   this.form.phone = JSON.parse(
    //     sessionStorage.getItem("productData")
    //   ).plchdMoveTelNo;
    // }
    // if (JSON.parse(sessionStorage.getItem("productData")).plchdEmailAdr) {
    //   this.emailDisabled = true;

    //   this.form.email = JSON.parse(
    //     sessionStorage.getItem("productData")
    //   ).plchdEmailAdr;
    // }
    // 获取批次号
    this.batchno = JSON.parse(sessionStorage.getItem("planPage")).KDPC;
    console.log("批次号", this.batchno);
    // 如果配置了参数judgeParam，就不掉获取学校信息的参数
    if (!this.judgeParam) {
      this.getClassInfo();
    }
    //   获取当前日期
    let data = new Date();
    let year = data.getFullYear();
    let month = data.getMonth();
    let day = data.getDate();
    console.log(year, month, day);
    // 将现在日期赋值给日期选择器上
    this.currentDate = new Date(year, month, day);
    this.currentDateInsured = new Date(year, month, day);
  },
  mounted() {},
  methods: {
    // 改变电话号码的判断
    formPhoenChange() {
      return this.phoneValidator();
    },
    //   点击提交 ，正确的时候
    onSubmit(values) {
      console.log("成功的values", values);
      console.log("form的值", this.form);
      sessionStorage.setItem("MORE_PAGE_from", JSON.stringify(this.form));
      this.checkCaptcha();
    },
    // 短信验证码的验证
    async checkCaptcha() {
      let params = {
        smsCode: this.form.SMSCode,
        prdId: JSON.parse(sessionStorage.getItem("productData")).prd_id,
        mobileNo: this.form.phone.replace(/\s/g, ""),
      };
      console.log(params);
      let res = await this.$api.common.checkCaptcha(params);
      if (res) {
        this.$loading.hide();
        if (res.status === "1") {
          sessionStorage.setItem("SINGE_PAGE_orderfode", res.data.orderCode);
          this.$router.push({
            path: "/productTerms",
          });
        } else {
          this.$toast(res.error.message);
        }
      }
    },
    // 姓名校验

    userNameValidator() {
      return util.checkName(this.form.userName);
    },

    userNameInsuredValidator() {
      return util.checkName(this.form.userNameInsured);
    },
    // 身份证格式的验证
    idCardValidator() {
      return isIdcard(this.form.idCard);
    },
    idCardValidatorInsured() {
      return isIdcard(this.form.idCardInsured);
    },
    // 手机号格式的验证
    phoneValidator() {
      return /^1[3|4|5|6|7|8|9][0-9]\d{8}$/.test(this.form.phone);
    },
    // 验证码6位判断
    SMSCodeValidator() {
      if (this.form.SMSCode && this.form.SMSCode.length < 6) {
        return false;
      } else {
        return true;
      }
    },
    //  email格式的验证
    emailValidator() {
      var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
      if (this.form.email) {
        return reg.test(this.form.email);
      }
      return true;
    },
    // 选择身份证弹框中确认
    DateOnConfirm(time) {
      console.log(time.getFullYear());
      let day = time.getDate();
      if (day < 10) {
        day = "0" + day;
      }
      let setMonth = time.getMonth() + 1;
      if (setMonth >= 1 && setMonth <= 9) {
        setMonth = "0" + setMonth;
      }
      this.form.dateTime = `${time.getFullYear()}-${setMonth}-${day}`;
      this.DateShowPicker = false;
    },
    DateOnConfirmInsured(time) {
      let day = time.getDate();
      if (day < 10) {
        day = "0" + day;
      }
      let setMonth = time.getMonth() + 1;
      if (setMonth >= 1 && setMonth <= 9) {
        setMonth = "0" + setMonth;
      }
      this.form.dateTimeInsured = `${time.getFullYear()}-${setMonth}-${day}`;
      this.DateShowPickerInsured = false;
    },
    // 点击长期有效
    longTime() {
      this.form.dateTime = "长期有效";
    },
    longTimeInsured() {
      this.form.dateTimeInsured = "长期有效";
    },
    // 短信验证码倒计时
    SMSCodeCountdown() {
      let time = 60;
      let countdown = setInterval(() => {
        time--;
        this.SMSCodeButText = time + "秒后重试";
        if (time == 0) {
          this.SMSCodeButText = "重新获取";
          this.SMSCodeDisable = false;
          clearInterval(countdown);
        }
      }, 1000);
    },
    changeSlideVerifyShow() {
      this.onSuccess();
    },
    // 获取imgCode
    getGenerateImgDragVerify(dataInfo) {
      this.$api.common
        .getGenerateImgDragVerify({
          randomId: this.getRandomId(),
        })
        .then((res) => {
          console.log(res, "imgcode");
          this.$loading.hide();
          if (res.status === "1") {
            this.imageCode = res.data.dragWidth / 3 - 10;
            this.showOverlay = true;
          } else if (res.status === "0") {
            this.$toast(res.error.message);
          }
        });
    },
    // 获取短信验证码
    getSMSCode() {
      if (this.formPhoenChange()) {
        this.getGenerateImgDragVerify();
        this.slideVerifyShow = true;
        this.showOverlay = true;
        this.slideVerifyShow = true;
        this.showJigsaw = true;
      } else {
        this.$toast("手机号格式错误！");
      }
    },
    getRandomId() {
      this.randomId = +new Date() + Math.random() * 100000 + 100000;
      return this.randomId;
    },
    // 获取短信验证码成功
    onSuccess() {
      this.slideVerifyShow = false;
      // 获取一个随机数

      let a = {
        mobileNo: this.form.phone.replace(/\s/g, ""),
        imageCode: this.imageCode,
        random: this.randomId,
        txCode: "CCBSB100",
      };
      // 获取短信验证码的接口
      this.$api.common.getCaptcha(a).then((res) => {
        console.log(res, "短信");
        this.$loading.hide();
        if (res.status === "1") {
          Toast.success("验证码获取成功");
          this.showOverlay = false;
        } else {
          this.$toast(res.error.message);
          this.showOverlay = false;
        }
      });

      this.SMSCodeDisable = true;
      this.SMSCodeCountdown();
    },
    // 获取短信验证码失败
    onFail() {
      console.log("获取短信验证码失败");
    },
    // 点击刷新滑块
    onRefresh() {
      console.log("点击刷新滑块");
    },
    // 滑块组件
    jigsawSuccess() {
      this.showJigsaw = false;
      // this.getCaptcha();
      this.onSuccess();
    },
    // 年级、选择
    gradeChooseOnConfirm(value, e) {
      // 切换年级 重置班级
      this.classArray = [];
      this.classColumnsClass = [];
      this.form.gradeChoose = value;
      this.gradeChooseShowPicker = false;

      // 班级数据
      this.classColumns = this.dataList.mapList;
      let classArrayitem = Object.values(this.dataList.mapList[e]);
      classArrayitem[0].forEach((element) => {
        this.classArray.push(element);
        this.classColumnsClass.push(element.className);
      });
      this.classColumns = this.classColumnsClass;
    },
    // 班级、选择
    classChooseOnConfirm(value, e) {
      this.form.classChoose = value;
      this.classChooseShowPicker = false;
      // 获取班级id
      this.form.classId = this.classArray[e].id;
    },
    // 获取学校班级年级
    getClassInfo() {
      // 班级数据
      // this.dataList = {
      //   name: "希望小学",
      //   mapList: [
      //     {
      //       一年级: [
      //         { id: "123", name: "1班" },
      //         { id: "124", name: "2班" },
      //       ],
      //     },
      //     { 二年级: [{ id: "123", name: "1班" }] },
      //     { 三年级: [{ id: "124", name: "2班" }] },
      //     {
      //       四年级: [
      //         { id: "123", name: "1班" },
      //         { id: "124", name: "2班" },
      //       ],
      //     },
      //     { 五年级: [{ id: "123", name: "1班" }] },
      //   ],
      // };
      // 获取班级信息接口
      let params = {
        batchno: this.batchno,
      };
      this.$api.common.getClassInfo(params).then((res) => {
        if (res.status === "1") {
          if (res.data) {
            this.dataList = res.data;
            // 学校
            this.form.school = this.dataList.schoolName;
            // 年级
            // this.gradeColumns = Object.keys(data.mapList)
            this.dataList.mapList.forEach((element) => {
              this.gradeColumnsClass.push(Object.keys(element)[0]);
            });
            this.gradeColumns = this.gradeColumnsClass;
          }
        } else {
          this.$toast(res.error.message);
        }
      });
    },
    // 地区
    AreaonConfirm(values) {
      console.log("地区", values);
      this.form.area = values
        .filter((item) => !!item)
        .map((item) => item.name)
        .join("/");
      this.showArea = false;
    }, // 扫描身份证
