const util = {}
//获取年龄
util.getAge = function (cardNo) {
  cardNo = cardNo.replace(/\s/g, '')
  if (cardNo.length < 18) return null;
  let myDate = new Date();
  let month = myDate.getMonth() + 1;
  let day = myDate.getDate();
  let age = myDate.getFullYear() - cardNo.substring(6, 10) - 1;
  let birthDay = cardNo.substring(12, 14)
  let birthMonth = cardNo.substring(10, 12)
  if (birthMonth < month || (birthMonth == month && birthDay < (day + 1))) {
    age++;
  }
  return age;
}
// 获取性别
util.getSex = function (cardNo) {
  cardNo = cardNo.replace(/\s/g, '')
  if (cardNo.length < 18) return null;
  // if(((parseInt(cardNo.substr(16, 1)) + 1) % 2 + 1) == 1){
  if(parseInt(cardNo.substr(16,1) % 2) == 1){
    return '01';
  }else{
    return '02';
  }
}

util.getSexOld = function (cardNo) {
  cardNo = cardNo.replace(/\s/g, '')
  if (cardNo.length < 18) return null;
  // if(((parseInt(cardNo.substr(16, 1)) + 1) % 2 + 1) == 1){
  return (parseInt(cardNo.substr(16, 1)) + 1) % 2 + 1;
}
//获取出生日期
util.getBirthday = function (cardNo) {
  cardNo = cardNo.replace(/\s/g, '')
  let birth = cardNo.substring(6, 10) + "/" + cardNo.substring(10, 12) + "/" + cardNo.substring(12, 14);
  return birth;
}

// 身份证校验
const _checkCode = function (val) {
  let p = /^[1-9]\d{5}(18|19|20)\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;
  let factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
  let parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2];
  let code = val.substring(17);
  if (p.test(val)) {
    let sum = 0;
    for (let i = 0; i < 17; i++) {
      sum += val[i] * factor[i];
    }
    if (parity[sum % 11] == code.toUpperCase()) {
      return true;
    }
  }
  return false;
}
const _checkDate = function (val) {
  let pattern = /^(18|19|20)\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)$/;
  if (pattern.test(val)) {
    let year = val.substring(0, 4);
    let month = val.substring(4, 6);
    let date = val.substring(6, 8);
    let date2 = new Date(year + "-" + month + "-" + date);
    if (date2 && date2.getMonth() == (parseInt(month) - 1)) {
      return true;
    }
  }
  return false;
}
const _checkProv = function (val) {
  let pattern = /^[1-9][0-9]/;
  let provs = { 11: "北京", 12: "天津", 13: "河北", 14: "山西", 15: "内蒙古", 21: "辽宁", 22: "吉林", 23: "黑龙江 ", 31: "上海", 32: "江苏", 33: "浙江", 34: "安徽", 35: "福建", 36: "江西", 37: "山东", 41: "河南", 42: "湖北 ", 43: "湖南", 44: "广东", 45: "广西", 46: "海南", 50: "重庆", 51: "四川", 52: "贵州", 53: "云南", 54: "西藏 ", 61: "陕西", 62: "甘肃", 63: "青海", 64: "宁夏", 65: "新疆", 71: "台湾", 81: "香港", 82: "澳门" };
  if (pattern.test(val)) {
    if (provs[val]) {
      return true;
    }
  }
  return false;
}
util.checkIdCard = function (val) {
  val = val.replace(/\s/g, '')
  if (_checkCode(val)) {
    let date = val.substring(6, 14);
    if (_checkDate(date)) {
      if (_checkProv(val.substring(0, 2))) {
        return true;
      }
    }
  }
  return false;
}

util.checkEmail = function (email) {
  // eslint-disable-next-line
  const reg = /^([A-Za-z0-9_\-\.\u4e00-\u9fa5])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,8})$/;
  return reg.test(email);
};

util.checkPhone = function (phoneNo) {
  const reg = /^[1][2,3,4,5,6,7,8,9][0-9]{9}$/;
  phoneNo = phoneNo.replace(/\s/g, "");
  return reg.test(phoneNo)
}

util.checkName = function (name) {
  // eslint-disable-next-line
  const reg = /^[a-zA-Z\u4e00-\u9fa5\s\.\*·]{2,}$/;
  return reg.test(name);
};
util.safeValue = function (params, path = []) {
  return new Proxy(function () { }, {
    get: function (target, key) {
      return util.safeValue(params, path.concat(key));
    },
    apply: function (target, object, args) {
      let value = params;
      for (let i = 0; i < path.length; i++) {
        if (value == null) {
          break;
        }
        value = value[path[i]];
      }
      if (value === void 0) value = "";
      return value;
    },
  });
};
util.getArea = function (list, code) {
  if (!list || !code) return;
  let res = [];
  let provinceCode = code.substr(0, 2) + "0000";
  let cityCode = code.substr(0, 4) + "00";
  let provinceText = list.province_list[provinceCode];
  let cityText = list.city_list[cityCode];
  let countryText = list.county_list[code];
  res.push(
    {
      code: provinceCode,
      name: provinceText,
    },
    {
      code: cityCode,
      name: cityText,
    },
    {
      code: code,
      name: countryText,
    }
  );
  return res;
};
// 防抖函数
util.debounce = function (fn, delay = 1000) {
  let timer;
  return function () {
    let argument = arguments;
    if (timer) clearTimeout(timer)
    timer = setTimeout(() => {
      fn && fn.apply(this,argument);
    }, delay)
  }
}
// 节流函数
util.throttel = function (fn, delay = 1000) {
  let timer;
  return function () {
    let argument = arguments;
    if (timer) return;
    fn && fn.apply(this, argument);
    timer = true;
    setTimeout(() => {
      timer = false;
    }, delay)
  }
}
