export function getKey(Arr, keyName, key) {
  const hasKey = Arr.find(v => v[keyName] === key)
  return hasKey || {}
}
export function setMap($_map, $_ele) {
  const $_maps = $_ele.maps
  $_maps.forEach(v => {
    const { key, value } = getFirstKey(v);
    if (typeof value === "function") {
      $_map[key] = value($_ele.value)
    } else if (typeof value === "string") {
      $_map[key] = $_ele[value]
    }
  })
}
export function getFirstKey(option) {
  for (const key in option) {
    if (Object.prototype.hasOwnProperty.call(option, key)) {
      const value = option[key]
      return { key, value }
    }
  }
}
