function agescope(norm, item, birthday) {
  if (typeof norm[0] === 'string') return (+new Date() - +new Date(birthday)) / 86400000 > norm[0] && item < norm[1];
  return item >= norm[0] && item <= norm[1]
};

function filterKeys(mapKeys, v_map, map) {
  console.log(map);
  
  let flag = true;
  mapKeys.forEach(v => {
    const norm = v_map[v], item = map[v];
    switch (norm.type) {
      case "agescope":
        if (!agescope(norm.value, item, map.birthday)) flag = false;
        break;
      case "string":
        if (norm.value !== item) flag = false;
        break;
      case "number":
        if (norm.value !== item) flag = false;
        break;
      default:
        break;
    }
    // if(norm.type === 'agescope') {
    //   if(!agescope(norm.value,item,map.birthday)) flag = false ;
    // }else if(norm.type === "string") {
    //   if(norm.value !== item) flag = false;
    // }
  })
  return flag
};

export function filterMap(batchs, map) {
  return batchs.find(v => {
    return filterKeys(Object.keys(v.map), v.map, map) && v
  });
};

export function clearParams(str) {
  str += "";
  return str.replace(/\s|-|\//g, "");
};

export function getRelationShip(relation, gender,gender1) {                               
  // 关系转换以投保人为准  投保人  父子01  父女02  母子03  母女04  双向关系   判断性别
  if (relation == '01') {
    return '01'
  } else if(gender == '01' && gender1 == '01'){       // 01,性别男
    return '06'
  } else if(gender == '01' && gender1 !== '01'){        
    return '07'
  } else if(gender !== '01' && gender1 == '01'){
    return '08'
  } else if(gender !== '01' && gender1 !== '01'){
    return '09'
  }
};
