export function getlastDay(time) {
  const date = new Date(+new Date(time.replace(/-/g, '/')) - 86400000);
  let y = date.getFullYear(), m = date.getMonth() + 1, d = date.getDate();
  return y + '-' + (m > 9 ? m : `0${m}`) + '-' + (d > 9 ? d : `0${d}`);
};
export function getcheckDay(time) {
  const date = new Date(+new Date(time.replace(/-/g, '/')));
  let y = date.getFullYear(), m = date.getMonth() + 1, d = date.getDate();
  return y + '-' + (m > 9 ? m : `0${m}`) + '-' + (d > 9 ? d : `0${d}`);
};
export function getDay(time) {
  const date = new Date(+new Date(time.replace(/-/g, '/')));
  let y = date.getFullYear(), m = date.getMonth() + 1, d = date.getDate(),h = date.getHours();
  return y + '-' + (m > 9 ? m : `0${m}`) + '-' + (d > 9 ? d : `0${d}`)+" "+(h > 9 ? h : `0${h}`)+":00:00";
};
export function reconfigArr(array,index) {
  return (array.slice(index)||[]).concat(array.slice(0,index) || []) 
}

