<template>
  <div>
    <div v-if="radio==1">
      <singlePage></singlePage>
    </div>
    <div v-else>
      <morePage></morePage>
    </div>
  </div>
</template>

<script>
import singlePage from "./components/singlePage.vue";
import morePage from "./components/morePage.vue";
export default {
  name: "",
  components: { singlePage, morePage },

  data() {
    return {};
  },

  created() {},
  mounted() {
    sessionStorage.setItem('route_radio',this.radio)
  },
  methods: {},
  watch: {},
  computed: {
    radio() {
      // 从为谁投保传过来的值
      return this.$route.query.radio;
    },
  },
};
</script>

